
import { parse } from 'query-string';
import React, { useState, useEffect } from 'react';
import { useLocation, useParams } from 'react-router-dom';
import { requesicoes } from '../../../utils/services';
import './pautaCadeira.scss';
import './pauta.css';
import { Alert, Button, TextField } from '@mui/material';
import SaveIcon from '@material-ui/icons/Save';
import { makeStyles, } from '@material-ui/core/styles';
import Swal from 'sweetalert2';
import { Redirect } from 'react-router-dom';
import { MoonLoader } from 'react-spinners'; // Importe o componente BounceLoader


let estudantes = [];
let cadeira = '';
let testes = [];
let avaliacoes = [];

let tiposAvaliacaoParaRemover = ['Exame', 'Rec'];

const useStyles = makeStyles((theme) => ({
  root: {
    '&:hover': {
      backgroundColor: 'transparent',
    },
  },

}));

const PautaCadeira = () => {

  const location = useLocation();
  const idCadeira = atob(parse(location.search).cadeira);
  const idCurso = atob(parse(location.search).curso);
  const turma = atob(parse(location.search).turma);
  const [to, setTo] = useState('');
  const [redirect, setRedirect] = useState(false);
  const [notasModificadas, setNotasModificadas] = useState([]);
  const classes = useStyles();

  const parametros = useParams();

  let id = window.location.href.split('/');
  id = (id[id.length - 1]);


  const [updateDom, setUpdateDom] = useState(false);
  const [notas, setNotas] = useState([]);
  const [carregando, setCarregando] = useState(true);


  useEffect(() => {
    init();
    setTimeout(() => {
      setCarregando(false);
    }, 3000);
  }, [])


  async function init() {

    const [avaliacoesRequest, listaEstudantes, testeRequest] = await Promise.all([
      requesicoes().getAvaliacoesByCadeiraAndTurma(`/${idCadeira}/${turma}`),
      requesicoes().getEstudanteTurma(`/${turma}/${idCadeira}?campos=nome,id,apelido,numero,matricula.id,matricula.anoFrequencia,matricula.turma,curso.id,curso.nome`),
      requesicoes().getTesteByCurso(`/${idCurso}/${idCadeira}?campos=id,nota,avaliacao,estudante.id,estudante.nome,estudante.apelido,estudante.numero`)]);//estudante.matricula

    estudantes = listaEstudantes.data;
    testes = testeRequest.data;
    avaliacoes = avaliacoesRequest.data;
    if (testes.length === 0) {
      testes = estudantes;
    } else {
      testes = testes.filter((teste) => teste.avaliacao.tipoAvaliacao !== 'Exame' && teste.avaliacao.tipoAvaliacao !== 'Recorrencia');

    }

    if (!avaliacoes || avaliacoes.length === 0) {

      Swal.fire({
        icon: 'warning',
        title: 'Oops...',
        text: 'Avaliações não estão definidas ou estão vazias!',
        footer: 'Por favor, defina o número de avaliações nesta cadeira!'
      }).then(() => {
        setTo('/teacher/notas'); // Define a URL para redirecionamento
        setRedirect(true); // Define o estado de redirecionamento como verdadeiro
      });
      testes = []
    }
    let contemValoresExameRecorencia = tiposAvaliacaoParaRemover.every(valor => avaliacoes.some(a => a.tipoAvaliacao === valor));

    if (contemValoresExameRecorencia) {

      let novoArray = avaliacoes.filter(a => !tiposAvaliacaoParaRemover.includes(a.tipoAvaliacao));
      avaliacoes = novoArray;
    }
    const testesPorEstudante = {};
    // testesFiltrados.forEach((teste) => {
    testes.forEach((teste) => {


      const estudanteId = teste.estudante === undefined ? teste.id : teste.estudante.id;

      if (!testesPorEstudante[estudanteId]) {

        testesPorEstudante[estudanteId] = { ...teste, notas: {} };

      }
      try {
        const avaliacao = avaliacoes.find(
          (a) =>
            a.tipoAvaliacao === teste.avaliacao.tipoAvaliacao && a.ordem === teste.avaliacao.ordem

        );
        const avaliacaoId = avaliacao ? avaliacao.id : null;
        testesPorEstudante[estudanteId].notas[avaliacaoId] = teste.nota || "";

      } catch (error) {
        const avaliacao = avaliacoes.find(
          (a) =>

            a.tipoAvaliacao === 'Teste' && a.ordem === 1
        );
        const avaliacaoId = avaliacao ? avaliacao.id : null;
        testesPorEstudante[estudanteId].notas[avaliacaoId] = teste.nota || "";
      }
    });

    setNotas(Object.values(testesPorEstudante));
    setCarregando(false);
    setUpdateDom(!updateDom);


  }
  const handleNotaChange = (estudanteId, avaliacaoId, event) => {

    const newValue = event.target.value;
    if (!isNaN(newValue) && newValue >= 0 && newValue <= 20) {
      const novasNotas = notas.map((notaEstudante) => {
        const estudanteIdAUX = notaEstudante.estudante === undefined ? notaEstudante.id : notaEstudante.estudante.id;
        if (estudanteIdAUX === estudanteId) {
          const notasModificadasCopy = [...notasModificadas];
          const notaModificada = notasModificadasCopy.find(
            (nota) => nota.estudanteId === estudanteId && nota.avaliacaoId === avaliacaoId
          );

          if (notaModificada) {
            notaModificada.nota = event.target.value;
          } else {
            notasModificadasCopy.push({
              estudanteId,
              avaliacaoId,
              nota: event.target.value,
            });
          }
          setNotasModificadas(notasModificadasCopy);
          return {
            ...notaEstudante,
            notas: {
              ...notaEstudante.notas,
              [avaliacaoId]: event.target.value,
            },
          };
        }
        return notaEstudante;
      });
      setNotas(novasNotas);
    }
  };
  async function handleSalvar() {
    try {
      const notasEnviadas = notasModificadas.map((notaModificada) => {
        const { estudanteId, avaliacaoId, nota } = notaModificada;
        return {
          estudante: { id: estudanteId },
          nota,
          avaliacao: { id: avaliacaoId },
          cadeira: { id: idCadeira },
        };
      });
      await Promise.resolve(requesicoes().postTeste(notasEnviadas));
      Swal.fire(
        'Exito!',
        'Notas lançadas com sucesso!',
        'success'
      )
      await init();

    }
    catch (e) {
      Swal.fire({
        icon: 'error',
        title: 'Oops...',
        text: e.response.data.message,
        footer: 'Se o erro persistir! Porfavor entre em conatcto conosco centroinformatico@am.ac.mz'
      })

    }
  };

  const getMediaClass = (media) => {
    return media !== "" && parseFloat(media) < 9.5 ? "media-red" : "";
  };

  const calculateMedia = (notas) => {
    const notasArray = Object.values(notas);
    const notasValidas = notasArray.filter((nota) => nota !== "" && nota !== null && !isNaN(parseFloat(nota)));
    if (notasValidas.length >= 3) {
      const sum = notasValidas.reduce((acc, nota) => acc + parseFloat(nota), 0);
      return (sum / notasValidas.length).toFixed(2);
    }

    return "";
  };

  if (redirect) {
    return <Redirect to={to} />;
  }

  

  return (
    <div>
      {carregando ? (
        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', marginTop: '20%' }}>
          <MoonLoader color="#36d7b7" />
        </div>

      ) :
        (<div>
          {testes.length !== 0 ? (
            <table className="AppTable">

              <thead>
                <tr>
                  <th>Numero</th>
                  <th>Nome Completo</th>

                  {avaliacoes.map((a, i) => (
                    <th className="right">{a.tipoAvaliacao}-{a.ordem} </th>
                  ))}

                  <th className="right">Media</th>
                  <th className="right">Observacao</th>
                </tr>
              </thead>
              <tbody>
                {notas.map((notaEstudante) => (
                  <tr key={notaEstudante.estudante === undefined ? notaEstudante.id : notaEstudante.estudante.id}>
                    <td>{notaEstudante.estudante === undefined ? notaEstudante.numero : notaEstudante.estudante.numero}</td>
                    <td>
                      {notaEstudante.estudante === undefined ? notaEstudante.nome : notaEstudante.estudante.nome} {notaEstudante.estudante === undefined ? notaEstudante.apelido : notaEstudante.estudante.apelido}
                    </td>
                    {avaliacoes.map((avaliacao) => {
                      const avaliacaoId = avaliacao.id;
                      const nota =
                        notaEstudante.notas[avaliacaoId] !== undefined
                          ? notaEstudante.notas[avaliacaoId]
                          : "";
                      const isNotaSet = nota !== "" && nota !== null && !isNaN(parseFloat(nota));

                      return (
                        <td key={avaliacaoId} className="right">
                          <TextField
                            className="imput"
                            id={`nota-${notaEstudante.id}-${avaliacaoId}`}
                            value={nota}
                            onChange={(event) =>
                              handleNotaChange(notaEstudante.estudante == undefined ? notaEstudante.id : notaEstudante.estudante.id, avaliacaoId, event)
                            }
                          // disabled={isNotaSet}
                          />

                        </td>
                      );
                    })}
                    <td className={`right ${getMediaClass(calculateMedia(notaEstudante.notas))}`}>{calculateMedia(notaEstudante.notas)}</td>
                    <td className={`right ${getMediaClass(calculateMedia(notaEstudante.notas))}`}>{calculateMedia(notaEstudante.notas) >= 13.5 ? 'Dispensado' : calculateMedia(notaEstudante.notas) < 9.5 ? calculateMedia(notaEstudante.notas) === "" ? '' : 'Excluido' : 'Admitido'}</td>
                  </tr>
                ))}
              </tbody>
            </table>

          ) : (<Alert severity="warning">Esta cadeira não ainda não tem estudantes matriculados. Pode se dar caso em que ela decorre no 2º Semestre. Se tens certeza que ela esta a decorrer por favor contacte o seu departamento para regularização.</Alert>)
          }
          {testes !== 0 && (<Button variant="contained" color="primary" type='submit' size="large" onClick={handleSalvar}
            className="btnSalvar" startIcon={<SaveIcon />}>
            Guardar
          </Button>)}


        </div>)}

    </div>

  )
}
export default PautaCadeira